.b2b {
  padding-top: clamp(64px, 10vw, 120px);
  padding-bottom: clamp(64px, 10vw, 120px);

  &__textWrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 16px;
    h2 {
      color: $text-color;
      font-family: Geologica;
      font-size: clamp(32px, 5vw, 48px);
      font-style: normal;
      font-weight: 500;
      line-height: 130%; /* 62.4px */
      max-width: 531px;
    }

    p {
      //   margin-top: 16px;
      max-width: 570px;
      color: $secondary-color;
      font-family: Open Sans;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 150%; /* 24px */
    }
  }

  &__cardWrapper {
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: clamp(16px, 3vw, 40px);

    &__card {
      width: clamp(160px, 30vw, 265px);
      height: clamp(225px, 40vw, 298px);
      border-radius: 20px;
      background: $grey-color;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: clamp(16px, 2vw, 20px);
      text-align: center;
      padding-top: clamp(24px, 3vw, 30px);
      padding-left: clamp(8px, 2vw, 52px);
      padding-right: clamp(8px, 2vw, 52px);

      h3 {
        color: $text-color;
        text-align: center;
        font-family: Geologica;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 150%; /* 24px */
      }
    }
    .card1 img {
      width: clamp(51px, 8vw, 72px);
    }

    .card2 img {
      width: clamp(77px, 12vw, 109px);
    }
    .card3 img {
      width: clamp(59px, 10vw, 83px);
    }
    .card4 img {
      width: clamp(104px, 16vw, 146px);
    }
  }
}
