.mobileHeader {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding-top: 24px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  &__logo {
    width: 170px;
    height: 28px;
  }

  .iconsWrapper {
    position: relative;
    width: 39px;
    height: 39px;
  }

  &__menu-icon {
    position: absolute;
    top: 0;
    left: 0;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
  }
}

@media (min-width: 767px) {
  .mobileHeader {
    display: none;
  }
}
