.hero {
  position: relative;
  height: 100svh;
  padding-top: clamp(127px, 20vh, 215px);
  background-image: url("../../../img/hero/hero-mobile.jpg");
  background-size: cover;
  background-position: center;

  h1 {
    color: $text-color;
    font-family: Geologica;
    font-size: clamp(34px, 5vw, 58px);
    font-style: normal;
    font-weight: 500;
    line-height: 120%; /* 43.2px */
  }

  &__descr {
    margin-top: 16px;
    max-width: 474px;
    color: $secondary-color;
    font-family: Open Sans;
    font-size: clamp(16px, 2vw, 18px);
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */
  }

  &__links {
    margin-top: 24px;
    display: flex;
    gap: 16px;

    &__link {
      position: relative;
      background: none;
      border: none;
      width: clamp(160px, 25vw, 178px);
      height: clamp(46px, 5vw, 50px);
      cursor: pointer;

      svg {
        height: 100%;
        display: block;
        width: 100%;
      }
    }
  }

  &__quote {
    position: absolute;
    bottom: clamp(56px, 10vh, 80px);
    padding: 16px;
    width: clamp(212px, 40vw, 380px);
    border-radius: 20px;
    opacity: 0.8;
    background: $grey-color;

    &__icon {
      position: absolute;
      top: -10px;
      right: 16px;
      width: clamp(30px, 3vw, 40px);
    }

    p {
      color: #131981;
      font-family: Geologica;
      font-size: clamp(16px, 2vw, 18px);
      font-style: italic;
      font-weight: 300;
      line-height: 150%; /* 24px */
    }
  }
}

@media (min-width: 767px) {
  .hero {
    background-image: url("../../../img/hero/hero.jpg");
    // background-position: top right;
  }
}
