.desktopHeader {
  padding-top: 24px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;

  nav {
    width: 388px;
    height: 52px;
    flex-shrink: 0;
    border-radius: 50px;
    background: rgba(255, 255, 255, 0.8);
    padding: 8px;

    ul {
      height: 100%;
      display: flex;
      li {
        height: 100%;
        list-style: none;
        border-radius: 30px;

        a {
          display: flex;
          align-items: center;
          padding: 0 20px;
          width: 100%;
          height: 100%;
          color: #171717;
          font-family: Open Sans;
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 130%; /* 20.8px */
          text-transform: capitalize;
          text-decoration: none;
        }

        &:hover {
          background-color: $blue;
          a {
            color: #fff;
          }
          svg path {
            fill: #fff;
          }
        }
      }
    }
  }

  .portal {
    margin-top: 16px;
    display: flex;
    align-items: center;
    background: transparent;
    text-decoration: none;
    border-radius: 30px;
    border: 1px solid transparent;
    transition: all 0.2s ease-in-out;

    &:hover {
      border: 1px solid $text-color;

      .portal__left {
        background: transparent;
        color: $text-color;
        border: 1px solid transparent;
      }
      .portal__right {
        border: 1px solid transparent;
      }
    }

    &__left {
      display: flex;
      flex: 1 0 0;
      padding: 13px 20px;
      justify-content: center;
      align-items: center;
      border-radius: 30px;
      border: 1px solid $text-color;
      background: $text-color;
      color: #fff;

      font-family: Geologica;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      transition: all 0.2s ease-in-out;
    }

    &__right {
      border-radius: 30px;
      border: 1px solid $text-color;
      width: 46px;
      height: 46px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 22px;
      transition: all 0.2s ease-in-out;
    }
  }
}

@media (max-width: 767px) {
  .desktopHeader {
    display: none;
  }
}
