.support {
  position: relative;
  padding-top: clamp(64px, 10vw, 100px);
  padding-bottom: clamp(64px, 10vw, 90px);
  background: #000000;

  picture {
    img {
      position: absolute;
      top: 0;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  h2 {
    color: #fff;
    font-family: Geologica;
    font-size: clamp(32px, 5vw, 48px);
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 41.6px */
  }

  &__wrapper {
    margin-top: clamp(32px, 5vw, 46px);
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    gap: clamp(16px, 3vw, 39px);

    .extraCard {
      padding: 0;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__card {
      border-radius: 20px;
      background: #fff;
      width: clamp(335px, 40vw, 367px);
      //   height: clamp(229px, 40vw, 260px);
      padding-top: clamp(24px, 3vw, 30px);
      padding-bottom: clamp(24px, 3vw, 30px);
      padding-left: clamp(20px, 3vw, 40px);
      padding-right: clamp(20px, 3vw, 40px);

      img {
        width: 32px;
        height: 32px;
      }

      h3 {
        margin-top: 8px;
        color: $text-color;
        font-family: Bricolage Grotesque;
        font-size: clamp(22px, 3vw, 24px);
        font-style: normal;
        font-weight: 500;
        line-height: 130%; /* 28.6px */
      }

      p {
        margin-top: 40px;
        color: $secondary-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%; /* 24px */
      }
    }
  }
}

@media (min-width: 767px) {
  .support {
    h2 {
      text-align: center;
    }
  }
}

@media (max-width: 1310px) {
  .support {
    &__wrapper {
      .extraCard {
        display: none;
      }
    }
  }
}
