.mobile-menu {
  height: 0;
  width: 100%;
  background: $grey-color;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 1;
  transition: all 0.3s ease-in-out;

  nav {
    padding-top: 127px;

    li {
      list-style: none;
      padding: 11px 20px;
      border-radius: 30px;
      background: #fff;

      a {
        display: block;
        text-decoration: none;
        color: $text-color;
        font-family: Open Sans;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 130%;
      }
    }

    .portal {
      margin-top: 16px;
      display: flex;
      align-items: center;
      background: transparent;
      text-decoration: none;
      border-radius: 30px;
      border: 1px solid transparent;
      transition: all 0.2s ease-in-out;

      &:hover {
        border: 1px solid $text-color;

        .portal__left {
          background: transparent;
          color: $text-color;
          border: 1px solid transparent;
        }
        .portal__right {
          border: 1px solid transparent;
        }
      }

      &__left {
        display: flex;
        flex: 1 0 0;
        padding: 13px 20px;
        justify-content: center;
        align-items: center;
        border-radius: 30px;
        border: 1px solid $text-color;
        background: $text-color;
        color: #fff;

        font-family: Geologica;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        transition: all 0.2s ease-in-out;
      }

      &__right {
        border-radius: 30px;
        border: 1px solid $text-color;
        width: 46px;
        height: 46px;
        flex-shrink: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 22px;
        transition: all 0.2s ease-in-out;
      }
    }

    li:not(:first-child) {
      margin-top: 16px;
    }
  }
}

.active-menu {
  height: 100%;
}

.hidden {
  opacity: 0;
}

@media (min-width: 767px) {
  .mobile-menu {
    display: none;
  }
}
